<template>
	<view>
		<wTitleBar
		title="活动列表"
		:isReturn='true' 
		bgColor="#213244" 
		textAlign='centers'
		textColor="#fff"></wTitleBar>
		</u-navbar>
		<view class='contentCons'>
			<view class='content_activity'>
				<view class="activity_content flex-row start">
					<mescroll-uni height="180%" top="445" ref="mescrollRef" @init="mescrollInit" @down="downCallback" @up="upCallback" :down="downOption" :up="upOption" >
						<view v-for="(item,index) in lb" class="action flex-column" @click.stop="openinfo('/pages/index/'+item.lj+item.id)">
							<image class='activity_img' :src='item.logo' :lazy-load='true'>
							<view class='activity_content_span flex-column'>
								<span>{{item.title}}</span>
								<span>{{item.time}}</span>
							</view>
						</view>
					</mescroll-uni>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import wTitleBar from '../../components/w-titleBar/w-titleBar.vue'
	import {
		AkxhHd
	} from '../../api/akxhhd.js'
	export default {
	    components:{wTitleBar},
		data() {
			return {
				lb:[],
			}
		},
		onLoad(option){
			this.init(option.pd);
		},
		methods: {
			init(pd){
				this.AkxhHdlists(pd);
			},
			AkxhHdlists(pd){
				if(pd == 1){
					AkxhHd('/catalyzer/AkxhHd/list','GET').then(res => {
						if (res.data.code == 200) {
							let result = res.data.result;
							let arr = [];
							result.forEach((items,index,array)=>{
								items.logo = this.imgURL + items.logo;
								items.time = items.endTime.substring(0,10);
								items.lj = 'activity';
								items.bt = '活动';
								arr.push(items);
							});
							this.lb = arr;
						}
					})
				}else{
					AkxhHd('/catalyzer/AkxhZx/list','GET').then(res => {
						if (res.data.code == 200) {
							let result = res.data.result;
							let arr = [];
							result.forEach((items,index,array)=>{
								items.logo = this.imgURL + items.logo;
								items.time = items.time.substring(0,10);
								items.lj = 'journalism';
								items.bt = '资讯';
								arr.push(items);
							});
							this.lb = arr;
						}
					})
				}
			}
		}
	}
</script>

<style>
	.flex-row {
	  display: flex;
	  justify-content: space-between;
	}
	.contentCons{
		background-color: #1c2a39;
		width:100vw;
		color:#fff;
		position: absolute;
	}
	.content_activity{
		width: 95vw;
		background-color: #14212d;
		margin: 15px 2.5vw;
	}
	.content_activity .activity_title span{
		font-size: 18px;
		color:#fff;
		padding: 5px 15px;
		font-weight: 600;
		width: 70%;
	}
	.content_activity .activity_title button{
		font-size:11px;
		line-height: 20px;
		margin: 5px;
		background-color: #304964;
		color:#f0f0f0;
	}
	.content_activity .activity_content{
		width: 100%;
		border: #2C405A solid 1px;
		height: 75px;
	}
	.content_activity .activity_content .activity_img{
		width: 30%;
		height: 75px;
	}
	.content_activity .activity_content .activity_content_span{
		width: 60%;
	}
	.content_activity .activity_content .activity_content_span span{
		padding: 8px;
	}
	.activity_content_8d_img{
		width: 100%;
		height: 880px;
	}
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
	.logo {
		height: 300rpx;
		width: 700rpx;
		margin-top: 200rpx;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 50rpx;
	}
	
	.text_area {
		width: 90vw;
		margin: 0 5vw;
	}
	.text_area .text_li{
		width: 30vw;
		height: 50px;
		margin: 5px;
		text-align: center;
		line-height: 50px;
		border: #1c2a39 solid 1px;
		border-radius: 8px;
		background: #1c2a39;
		background-image: linear-gradient(#151f2b 0%,#1c2939 30%,#375070 60%,#455570 100%);
		color:#e4cea5;
		font-size: 15px;
	}
</style>
